@use 'sass:math';

.product {
    max-width: 320px;

    position: relative;
    display: flex;
    flex-direction: column;

    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    // .product__discount
    &__discount {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;

        display: block;
        width: 68px;
        height: 68px;
        padding: 14px 0 0 8px;

        color: var(--main-white);
        font-size: 19px;
        font-weight: 700;
        letter-spacing: math.div(.3px,19px) * 1em;
        line-height: math.div(24px,19px) * 100%;

        background-color: var(--discount-color);
        border-radius: 0 0 100% 0;

        @media (max-width:$md4+px){
            width: 33px;
            height: 33px;
            padding: 5px 0 0 4px;
            font-size: 10px;
            font-weight: 600;
            line-height: math.div(16px,10px) * 100%;
        }
    }
    // .product__favourites
    &__favourites {
        position: absolute;
        z-index: 2;
        top: 15px;
        right: 6px;

        @media (max-width:$md4+px){
             top: 0;
             right: 0; 
        }
       
    }
    // .product__image-wrapper
    &__image-wrapper {
        margin-bottom: 43px;
        padding: 26px 10px 0;

        @media (max-width:$md4+px){
            margin-bottom: 13px; 
            padding: 15px 2px; 
       }
    }
    // .product__image
    &__image {
        width: 100%;
        max-width: 300px;
        padding: 0px 0px 60% 0px;
    }
    // .product__name
    &__name {
        margin-bottom: 10px;

        font-size: 16px;
        line-height: math.div(26px,16px) * 100%;
        text-align: left;

        @media (max-width:$md3+px){
             max-width: 115px; 
             font-size: 13px;
        }
        @media (max-width: 335px){
              font-size: 12px;
        }
    }
    // .product__year
    &__year {
        margin-bottom: 14px;

        font-size: 13px;
        line-height: 100%;
    }
    // .product__reviews
    &__reviews {
        margin-bottom: 15px;

        @media (max-width:$md4+px){
             margin-bottom: 4px; 
        }
    }
    // .product__colors
    &__colors {
        margin-bottom: 25px;

        @media (max-width:$md4+px){
             display: none; 
        }
    }
    // .product__prices
    &__prices {
        display: flex;
        align-items: flex-end;
        margin-bottom: 35px;

        letter-spacing: math.div(.3px,22px) * 1em;
        line-height: math.div(28px,22px) * 100%;

        @media (max-width:$md3+px){
            flex:  1 1 auto;
            flex-direction: column;
            align-items:flex-start; 
            margin-bottom: 15px;
        }
    }
    // .product__price
    &__price {
        // .product__price_new
        &_new {
            margin-right: 20px;

            font-size: 22px;
            font-weight: 700;

            @media (max-width:$md3+px){
                margin: 0;
            }
            @media (max-width:$md4+px){
                font-size: 16px;
                font-weight: 600; 
            }
        }
        // .product__price_old
        &_old {
            position: relative;
            flex: 0 1 84px;

            display: flex;
            justify-content: flex-end;

            color: var(--medium-gray);
            font-size: 19px;
            font-weight: 700;

            @media (max-width:$md3+px){
                text-decoration: line-through;
                flex: 0 1 auto;
            }
            

            @media (max-width:$md4+px){
                 font-size: 10px;
                 font-weight: 600;
            }
            &::before{
                content:'';
                width: 110%;
                height: 1.5px;
                position: absolute;
                top: 50%;
                left: 0;

                background-color: var(--medium-gray);
                transform: translate(0px,50%);

                @media (max-width:$md3+px){
                     display: none; 
                }
            }
        }
    }
    // .product__bottom-row
    &__bottom-row {
        width: 100%;
        display: flex;
        align-items: center;
    }
    // .product__button
    &__button {
        position: relative;
        flex: 0 1 83.75%;
    }
    // .product__btn
    &__btn {
    }
    // .product__icon-cart
    &__icon-cart {
    }
    // .product__compare
    &__compare {
        flex: 1 0 auto;
        padding: 17px 10px;

        display: flex;
        justify-content:center;
        align-items: center;
        
        background-color: var(--main-white);

        cursor: pointer;

        transition: all 0.3s ease 0s;

        @media (any-hover: hover){
            &:hover{
                background-color:  var(--light-gray);

                svg {
                    --compare-hover-color: #111 ;
                }
            }
        }
        @media (max-width:$md4+px){
            padding: 9.5px 15px;
        }
    }
}
.reviews {
    // .reviews__empty
    &__empty {
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;
        color: var(--main-gray);
    }
    // .reviews__completed
    &__completed {
    }
    // .reviews__rating
    &__rating {
    }
    // .reviews__quantity
    &__quantity {
        color: var(--main-gray);
        font-size: 13px;
        line-height: math.div(21px,13px) * 100%;

        @media (max-width:$md4+px){
            font-size: 10px; 
        }
    }
}
.color-scheme-product {
    display: inline-flex;
  
    padding: 2px;

    border-radius: 3px;
    border: 1px solid transparent;
    cursor: pointer;

    transition: border-color 0.3s ease 0s;

    &._active{
        border-color: var(--main-yellow);
    }

    &__wrapper {
        position: relative;
        width: 32px;
        height: 19px;
        display: flex;
        justify-content:center;
        align-items: center;

    }
    // .color-scheme-product__main
    &__main {
        width: 100%;
        height: 100%;
    
        background-color: #000;
        border-radius: 3px ;

        clip-path: polygon(0 0, 67% 0, 41% 100%, 0% 100%);
    }

    // .color-scheme-product__second
    &__second {
        position: absolute;
        top: 0px;
        right: 0px;
        width: 100%;
        height: 100%;;
        border-radius: 3px;


        clip-path: polygon(66% 0, 100% 0%, 100% 100%, 40% 100%);

        // .color-scheme-product__color_yellow
        &_yellow {
            background-color: var(--main-yellow);
        }
        // .color-scheme-product__color_white
        &_white {
            background-color: var(--dark-white);
        }
    }
}


