@use 'sass:math';
.about-us {
    // .about-us__container
    &__container {
        @media (max-width:330px){
             padding: 0; 
        }
    }
    // .about-us__title
    &__title {
        margin: 0px 0px math.div(51px,34px) * 1em 0px; 
        text-align-last: left;

        @media (max-width:$md4+px){
             margin-bottom: 16px; 
        }
        @media (max-width: 330px){
            padding-left: 15px;
        }
    }
    // .about-us__content
    &__content {
    }
    // .about-us__text
    &__text {
        max-width: 382px;
        font-size: 19px;
        line-height: math.div(32px,19px) * 100%;

        @media (max-width:$md4+px){
            max-width: 240px;
            padding-bottom: 32px;

            font-size: 13px; 
        }
        @media (max-width: 330px){
            padding-left: 15px;
        }
    }
    // .about-us__mountains
    &__mountains {

        display: flex;

        background: url('../img/desktop/page-home/mountains.svg') 0 bottom / contain no-repeat ;


        transition: background-position 1s ease 0.1s;
        overflow: hidden;

        background-position: 0 300%;

        @media (max-width:$md2+px){
            justify-content: center;
        }

        @media (max-width:$md4+px){
            background-size: 354px 50px;
        }

        &._active{
            background-position: 0 bottom;
        }
    }
    // .about-us__bike
    &__bike {
        position: relative;
        padding-top: 36px;

        transform: translate(800px,0px);

        transition: transform 3s linear 0.5s;

        @media (min-width: 1920px){
            transform: translate(1800px,0px);
        }
        @media (min-width: 1440px){
            transform: translate(1000px,0px);
        }

        @media (max-width:$md2+px){
            padding: 0;
            width: 270px;
            height: 154px;

            svg {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        @media (max-width:$md4+px){
            width: 193px;
            height: 110px;

            transform: translate(600px,0px);
            transition: transform 2s linear 0s;
        }

        &._active {
            transform: translate(210px,0px);
            @media (max-width:$md1+px){
                 transform: translate(0px,0px); 
            }
            @media (max-width:$md2+px){
                transform: translate(50px,0);
            }
            @media (max-width:$md4+px){
                transform: translate(0px,0);
            }

            .bike_circle{
                position: absolute;
                z-index: -1;
                width: 149px;
                height: 149px;
                display: block;

                background-color: #FDB92B;
                border-radius: 50%;

                transform: rotate(40deg);

                animation: spin 1s linear 3 .5s;

                @media (max-width:$md2+px){
                    width: 104px;
                    height: 104px;
                }
                @media (max-width:$md4+px){
                     width: 74.5px; 
                     height: 74.5px; 

                     animation-iteration-count: 1.5;
                }

                &::after{
                    content:'';
                    position: absolute;
                    top: 50%;
                    left: 0%;
                    width: 100%;
                    height: 3px;
                    background-color: #FFAC00;
    
                    transform: translate(0,-50%);
                }
                &::before{
                    content:'';
                    position: absolute;
                    top: 0%;
                    left: 50%;
                    width: 3px;
                    height: 100%;
                    background-color: #FFAC00;
    
                    transform: translate(-50%,0px);
                }
    
                &-1 {
                    top: 102px;
                    left: 3px;
                    @media (max-width:$md2+px){
                        top: 50px;
                        left: 3px;
                    }
                    @media (max-width:$md4+px){
                        top: 35px;
                        left: 2px;
                    }
                }
                &-2 {
                    top: 101px;
                    left: 229px;
                    @media (max-width:$md2+px){
                        top: 50px;
                        left: 161px;
                    }
                    @media (max-width:$md4+px){
                        top: 34px;
                        left: 116px;
                    }
                }
            }
        }
    }
}

@keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-360deg);
    }
  }
